	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Another Baby Hanson","date":"30 Jan 2000","slug":"/2000-01-30-another-baby-hanson/","tags":"Y2000"},"html":"<p>Congrats to Tom and Denise, who had a baby girl Friday evening! Tom is my cousin in northern Minnesota.</p>\n<p>Small World Dept: <a href=\"http://carpediem.editthispage.com\">David Theig</a> from Grand Forks has inlaws in <a href=\"http://CarpeDiem.editthispage.com/discuss/msgReader$83\">my hometown</a>.</p>\n<p><a href=\"http://www.aventbaby.com\"><img src=\"http://shanson.editthispage.com/picture$123\" height=\"80\" width=\"59\" border=\"0\" align=\"right\" /></a> </p>\n<p>We've been trying out our new <a href=\"http://www.aventbaby.com\">Avent</a> breast pump. Sometime I'm going to have to be able to feed Christopher when Mama's not here!</p>\n<p>Bill Gates took time off from Davos to appear Saturday evening on Wetten Dass, a talk/game show that is the highest rated German TV show. He looked <a href=\"http://212.227.136.156/slideshow/2/full/7.jpg\"> uncomfortable</a> between Naomi Campbell and <a href=\"http://212.227.136.156/slideshow/1/full/2.jpg\">Pamela Anderson</a>. <i>The ZDF network is a partner of Microsoft.</i></p>\n<p>The storm made a lot of noise last night, with winds up to 90 mph. Flooding on the coast was 'nothing unusual', but the next high tide at 11 am might also bring flooding. The Sunday morning 'Fischmarkt' has been cancelled.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0130.jpg\" height=\"305\" width=\"350\" border=\"0\" alt=\"crh0130.jpg: \" /></p>"},"next":{"fields":{"title":"Stormy Saturday","date":"29 Jan 2000","slug":"/2000-01-29-stormy-saturday/"}},"previous":{"fields":{"title":"Back on Monday","date":"31 Jan 2000","slug":"/2000-01-31-back-on-monday/"}}},{"node":{"fields":{"title":"Stormy Saturday","date":"29 Jan 2000","slug":"/2000-01-29-stormy-saturday/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/molehill.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"molehill.jpg: \" /></p>\n<p>Our big projects for today include getting some mole repellent for our friends in the back yard. Here you can see the path they are taking towards our patio.</p>\n<p><a href=\"http://www.scriping.com\">Dave Winer</a> gives us a glimpse of what it's like at the <a href=\"http://davenet.userland.com/2000/01/29/twoDaysAtDavos\">World Economic Forum in Davos</a></p>\n<p>I think the big stormstorm that hit the US East Coast earlier this week has now reached us as rain with heavy winds. Yech! Flooding is forecast for this evening for the North Sea coast and Hamburg harbor (including my office).</p>"},"next":{"fields":{"title":"Friday Afternoon","date":"28 Jan 2000","slug":"/2000-01-28-friday-afternoon/"}},"previous":{"fields":{"title":"Another Baby Hanson","date":"30 Jan 2000","slug":"/2000-01-30-another-baby-hanson/"}}},{"node":{"fields":{"title":"Friday Afternoon","date":"28 Jan 2000","slug":"/2000-01-28-friday-afternoon/","tags":"Y2000"},"html":"<p>I came home Friday evening to find Grandma Ruth had gone home, and that both Mama and Christopher were tired (and just a little bit cranky). I sent them both to bed. It seems Mama misses out on her afternoon nap when Grandma visits. </p>\n<p>No matter how old you get, there's nothing like being with your parents to make you feel childish again!</p>\n<p>At work I've been setting up a test apache server running under <a href=\"http://www.freebsd.org\">FreeBSD</a>. It's actually quite nice to work with!</p>\n<p>For the first time in 10 years, the dollar costs more than 2 DM. Expressed in modern terms, <a href=\"http://uk.news.yahoo.com/000128/5/dzad.html\">the Euro</a> since yesterday costs less than a dollar.</p>\n<hr />\nI'm typing this bit with the new Mozilla M13 under Linux. This Mozilla seems usable! <i>Or am I just tired of Netscape \"kill -9\" Communicator 4?</i> Even the highly styled <a href=\"http://array.editthispage.com\">array</a> looks almost like it's supposed to. <i>Netscape under Linux is a style killer!<br />\n</i></p>"},"next":{"fields":{"title":"Domain Grabbing","date":"27 Jan 2000","slug":"/2000-01-27-domain-grabbing/"}},"previous":{"fields":{"title":"Stormy Saturday","date":"29 Jan 2000","slug":"/2000-01-29-stormy-saturday/"}}}],"pathPrefix":"","first":false,"last":false,"index":1085,"pageCount":1101}}

